<script>
export default {
  data() {
    return {
      schedule: null
    };
  },

  created() {
    setInterval(this.fetchSchedule(), 1000);
  },
  methods: {
    fetchSchedule() {
      console.log("fetch new data");
      var vm = this;
      // var axios = require("axios");
      var qs = require("qs");
      var data = qs.stringify({
        mod: "web",
        action: "getAllSchedule"
      });
      var config = {
        method: "post",
        url: "https://api.bolalive4.tv/api.php",
        data: data
      };

      this.$http(config)
        .then(function(response) {
          vm.schedule = response.data.data;
        })
        .catch(function(error) {
          console.log(error);
        });
    },
    fetchSchedule2() {
